import {Pipe, PipeTransform} from "@angular/core";

@Pipe({name: 'hsyGroupEnumMsgPipe'})
export class HsyGroupEnumMsgPipe implements PipeTransform {
  transform(value):string {
    switch(value) {
      case 'SanFrancisco':
        return '';
      case 'SouthBayWest':
        return '';
      case 'SouthBayEast':
        return '';
      case 'EastBay':
        return '';
      case 'MidPeninsula':
        return '';
      case 'Seattle':
        return '';
      case 'ShortTerm':
        return '';
      case 'TestGroup':
      return '';
      default:
        return value;
    }
  }
}



