  signUpLabel: "",
  signUpSubmitLabel: "", // needs review
  signUpTerms: "",
  signUpWithLabel: " %s ",
  socialLoginInstructions: "",
  socialSignUpInstructions: "",
  ssoEnabled: "",
  submitLabel: "", // needs review
  unrecoverableError: "<br />",
  usernameFormatErrorHint: "%d-%d,  \"_\"",
  usernameInputPlaceholder: "",
  usernameOrEmailInputPlaceholder: "/",
  title: "",
  welcome: " %s!",
  windowsAuthInstructions: "&hellip;",
  windowsAuthLabel: "Windows"
